import React, { useState, useEffect } from 'react';
import {
  Card,
  Form,
  InputNumber,
  Select,
  Switch,
  Button,
  Space,
  Divider,
  Alert,
  Statistic,
  Row,
  Col,
  Typography,
  Tag,
  message,
  Modal,
  Progress
} from 'antd';
import {
  SettingOutlined,
  DatabaseOutlined,
  ClearOutlined,
  InfoCircleOutlined,
  ExclamationCircleOutlined
} from '@ant-design/icons';
import { useAppStore } from '@/stores/appStore';
import type { StorageConfig } from '@/utils/storageManager';
import { filesize } from 'filesize';
import styled from 'styled-components';

const { Title, Text } = Typography;
const { Option } = Select;

const SettingsContainer = styled.div`
  padding: 24px;
  max-width: 1200px;
  margin: 0 auto;
`;

const StatsCard = styled(Card)`
  .ant-statistic-content {
    color: #1890ff;
  }
`;

const SettingsPage: React.FC = () => {
  const [form] = Form.useForm();
  const [loading, setLoading] = useState(false);
  const [clearLoading, setClearLoading] = useState(false);
  const [storageStats, setStorageStats] = useState<any>(null);
  const [showAdvanced, setShowAdvanced] = useState(false);
  
  const {
    getStorageConfig,
    updateStorageConfig,
    getStorageStats,
    clearAllHistory
  } = useAppStore();

  // 
  useEffect(() => {
    loadSettings();
    loadStorageStats();
  }, []);

  const loadSettings = () => {
    const config = getStorageConfig();
    form.setFieldsValue(config);
  };

  const loadStorageStats = async () => {
    try {
      const stats = await getStorageStats();
      setStorageStats(stats);
    } catch (error) {
      console.error('Failed to load storage stats:', error);
    }
  };

  const handleSaveSettings = async (values: StorageConfig) => {
    try {
      setLoading(true);
      updateStorageConfig(values);
      message.success('');
      await loadStorageStats(); // 
    } catch (error) {
      message.error('');
    } finally {
      setLoading(false);
    }
  };

  const handleClearAllData = () => {
    Modal.confirm({
      title: '',
      content: '',
      icon: <ExclamationCircleOutlined />,
      okText: '',
      okType: 'danger',
      cancelText: '',
      onOk: async () => {
        try {
          setClearLoading(true);
          await clearAllHistory();
          await loadStorageStats();
          message.success('');
        } catch (error) {
          message.error('');
        } finally {
          setClearLoading(false);
        }
      }
    });
  };

  const getStorageUsageColor = () => {
    if (!storageStats) return '#1890ff';
    const usagePercent = (storageStats.totalSize / (form.getFieldValue('maxCacheSize') * 1024 * 1024)) * 100;
    if (usagePercent > 80) return '#ff4d4f';
    if (usagePercent > 60) return '#faad14';
    return '#52c41a';
  };

  const getStorageUsagePercent = () => {
    if (!storageStats) return 0;
    const maxSize = form.getFieldValue('maxCacheSize') * 1024 * 1024;
    return Math.min((storageStats.totalSize / maxSize) * 100, 100);
  };

  return (
    <SettingsContainer>
      <Card
        title={
          <Space>
            <SettingOutlined />
            <span></span>
          </Space>
        }
        extra={
          <Button
            type="link"
            onClick={() => setShowAdvanced(!showAdvanced)}
          >
            {showAdvanced ? '' : ''}
          </Button>
        }
      >
        {/*  */}
        {storageStats && (
          <div style={{ marginBottom: 32 }}>
            <Title level={4}>
              <DatabaseOutlined style={{ marginRight: 8 }} />
              
            </Title>
            
            <Row gutter={[16, 16]} style={{ marginBottom: 16 }}>
              <Col xs={12} sm={8} md={6}>
                <StatsCard size="small">
                  <Statistic
                    title=""
                    value={storageStats.itemCount}
                    suffix=""
                  />
                </StatsCard>
              </Col>
              <Col xs={12} sm={8} md={6}>
                <StatsCard size="small">
                  <Statistic
                    title=""
                    value={filesize(storageStats.totalSize || 0).toString()}
                  />
                </StatsCard>
              </Col>
              <Col xs={12} sm={8} md={6}>
                <StatsCard size="small">
                  <Statistic
                    title=""
                    value={storageStats.memoryCount}
                    suffix=""
                  />
                </StatsCard>
              </Col>
              <Col xs={12} sm={8} md={6}>
                <StatsCard size="small">
                  <Statistic
                    title=""
                    value={storageStats.indexedDBCount + storageStats.localStorageCount}
                    suffix=""
                  />
                </StatsCard>
              </Col>
            </Row>

            <div style={{ marginBottom: 16 }}>
              <Text strong></Text>
              <Progress
                percent={getStorageUsagePercent()}
                strokeColor={getStorageUsageColor()}
                format={() => `${filesize(storageStats.totalSize || 0)} / ${form.getFieldValue('maxCacheSize')}MB`}
              />
            </div>

            <Space wrap>
              <Tag color="green">IndexedDB: {storageStats.indexedDBCount} </Tag>
              <Tag color="blue">LocalStorage: {storageStats.localStorageCount} </Tag>
              <Tag color="orange">: {storageStats.memoryCount} </Tag>
            </Space>
          </div>
        )}

        <Divider />

        {/*  */}
        <Form
          form={form}
          layout="vertical"
          onFinish={handleSaveSettings}
          initialValues={{
            maxCacheSize: 100,
            maxHistoryCount: 50,
            compressionEnabled: true,
            preferredStorage: 'indexeddb',
            autoCleanup: true
          }}
        >
          <Title level={4}></Title>
          
          <Row gutter={[16, 0]}>
            <Col xs={24} sm={12} md={8}>
              <Form.Item
                label=""
                name="maxCacheSize"
                tooltip="MB"
                rules={[
                  { required: true, message: '' },
                  { type: 'number', min: 10, max: 1000, message: '10-1000MB' }
                ]}
              >
                <InputNumber
                  style={{ width: '100%' }}
                  min={10}
                  max={1000}
                  step={10}
                  addonAfter="MB"
                />
              </Form.Item>
            </Col>
            
            <Col xs={24} sm={12} md={8}>
              <Form.Item
                label=""
                name="maxHistoryCount"
                tooltip=""
                rules={[
                  { required: true, message: '' },
                  { type: 'number', min: 10, max: 500, message: '10-500' }
                ]}
              >
                <InputNumber
                  style={{ width: '100%' }}
                  min={10}
                  max={500}
                  step={5}
                  addonAfter=""
                />
              </Form.Item>
            </Col>
            
            <Col xs={24} sm={12} md={8}>
              <Form.Item
                label=""
                name="preferredStorage"
                tooltip=""
              >
                <Select>
                  <Option value="indexeddb">IndexedDB ()</Option>
                  <Option value="localstorage">LocalStorage</Option>
                  <Option value="memory"></Option>
                </Select>
              </Form.Item>
            </Col>
          </Row>

          <Row gutter={[16, 0]}>
            <Col xs={24} sm={12}>
              <Form.Item
                label=""
                name="compressionEnabled"
                valuePropName="checked"
                tooltip="CPU"
              >
                <Switch />
              </Form.Item>
            </Col>
            
            <Col xs={24} sm={12}>
              <Form.Item
                label=""
                name="autoCleanup"
                valuePropName="checked"
                tooltip=""
              >
                <Switch />
              </Form.Item>
            </Col>
          </Row>

          {showAdvanced && (
            <>
              <Divider />
              <Title level={4}></Title>
              
              <Alert
                type="info"
                showIcon
                icon={<InfoCircleOutlined />}
                message=""
                description=""
                style={{ marginBottom: 16 }}
              />

              <Text type="secondary">
                
              </Text>
            </>
          )}

          <Divider />

          <Space>
            <Button
              type="primary"
              htmlType="submit"
              loading={loading}
              icon={<SettingOutlined />}
            >
              
            </Button>
            
            <Button
              onClick={loadSettings}
            >
              
            </Button>
            
            <Button
              danger
              onClick={handleClearAllData}
              loading={clearLoading}
              icon={<ClearOutlined />}
            >
              
            </Button>
          </Space>
        </Form>
      </Card>
    </SettingsContainer>
  );
};

export default SettingsPage;