import React from 'react';
import { Card, Alert, List, Tag, Empty, Divider } from 'antd';
import { 
  ExclamationCircleOutlined,
  CloseCircleOutlined,
  WarningOutlined,
  BugOutlined
} from '@ant-design/icons';
import { useAnalysisResult } from '@/stores/appStore';
import type { ErrorDetail } from '@/types/har';

const ErrorsTab: React.FC = () => {
  const analysisResult = useAnalysisResult();

  if (!analysisResult) {
    return <div></div>;
  }

  const { errors } = analysisResult;

  const renderErrorList = (title: string, errorList: ErrorDetail[], icon: React.ReactNode, color: string) => {
    if (errorList.length === 0) {
      return null;
    }

    return (
      <Card 
        title={
          <span>
            {icon}
            <span style={{ marginLeft: 8 }}>{title}</span>
            <Tag color={color} style={{ marginLeft: 8 }}>
              {errorList.length}
            </Tag>
          </span>
        }
        style={{ marginBottom: 16 }}
      >
        <List
          size="small"
          dataSource={errorList}
          renderItem={(error, index) => (
            <List.Item>
              <List.Item.Meta
                title={
                  <div style={{ display: 'flex', alignItems: 'center', gap: 8 }}>
                    <Tag color={getMethodColor(error.method)}>{error.method}</Tag>
                    <Tag color={color}>{error.status} {error.statusText}</Tag>
                    <span style={{ fontSize: 12, color: '#666' }}>
                      {Math.round(error.responseTime)}ms
                    </span>
                  </div>
                }
                description={
                  <div>
                    <div style={{ marginBottom: 4, wordBreak: 'break-all' }}>
                      {error.url}
                    </div>
                    <div style={{ fontSize: 12, color: '#999' }}>
                      : {error.startTime.toLocaleString()}
                    </div>
                  </div>
                }
              />
            </List.Item>
          )}
          pagination={errorList.length > 10 ? {
            pageSize: 10,
            size: 'small',
            showSizeChanger: false
          } : false}
        />
      </Card>
    );
  };

  const getMethodColor = (method: string): string => {
    const colors: Record<string, string> = {
      'GET': 'blue',
      'POST': 'green',
      'PUT': 'orange',
      'DELETE': 'red',
      'PATCH': 'purple'
    };
    return colors[method] || 'default';
  };

  // 
  const totalErrors = errors.totalErrors;
  const errorRate = errors.errorRate;

  return (
    <div style={{ padding: '24px 0' }}>
      {/*  */}
      <Alert
        message=""
        description={
          <div>
            <p>
               <strong>{totalErrors}</strong>  <strong>{errorRate.toFixed(2)}%</strong>
            </p>
            <div style={{ display: 'flex', gap: 16, flexWrap: 'wrap' }}>
              <span>4xx : <Tag color="orange">{errors.clientErrors.length}</Tag></span>
              <span>5xx : <Tag color="red">{errors.serverErrors.length}</Tag></span>
              <span>: <Tag color="red">{errors.networkErrors.length}</Tag></span>
            </div>
          </div>
        }
        type={totalErrors > 0 ? 'warning' : 'success'}
        showIcon
        style={{ marginBottom: 24 }}
      />

      {/*  */}
      {totalErrors === 0 && (
        <Card>
          <Empty
            image={Empty.PRESENTED_IMAGE_SIMPLE}
            description={
              <div>
                <div style={{ fontSize: 16, marginBottom: 8 }}> </div>
                <div></div>
              </div>
            }
          />
        </Card>
      )}

      {/*  (4xx) */}
      {renderErrorList(
        ' (4xx)',
        errors.clientErrors,
        <WarningOutlined />,
        'orange'
      )}

      {/*  (5xx) */}
      {renderErrorList(
        ' (5xx)',
        errors.serverErrors,
        <CloseCircleOutlined />,
        'red'
      )}

      {/*  */}
      {renderErrorList(
        '',
        errors.networkErrors,
        <BugOutlined />,
        'red'
      )}

      {/*  */}
      {totalErrors > 0 && (
        <Card title="" style={{ marginTop: 16 }}>
          <div>
            <h4>:</h4>
            <List size="small">
              {errors.clientErrors.length > 0 && (
                <List.Item>
                  <List.Item.Meta
                    avatar={<WarningOutlined style={{ color: '#faad14' }} />}
                    title="4xx "
                    description="URL"
                  />
                </List.Item>
              )}
              {errors.serverErrors.length > 0 && (
                <List.Item>
                  <List.Item.Meta
                    avatar={<CloseCircleOutlined style={{ color: '#f5222d' }} />}
                    title="5xx "
                    description=""
                  />
                </List.Item>
              )}
              {errors.networkErrors.length > 0 && (
                <List.Item>
                  <List.Item.Meta
                    avatar={<BugOutlined style={{ color: '#f5222d' }} />}
                    title=""
                    description="DNS"
                  />
                </List.Item>
              )}
            </List>

            <Divider />

            <h4>:</h4>
            <List size="small">
              <List.Item>
                <ExclamationCircleOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
              <List.Item>
                <ExclamationCircleOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
              <List.Item>
                <ExclamationCircleOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
              <List.Item>
                <ExclamationCircleOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                API
              </List.Item>
            </List>
          </div>
        </Card>
      )}
    </div>
  );
};

export default ErrorsTab;