import React, { useState, useCallback } from 'react';
import { 
  Card, 
  Upload, 
  Typography, 
  Alert, 
  Steps, 
  Space, 
  Spin, 
  Result,
  Button,
  message,
  Progress
} from 'antd';
import { 
  InboxOutlined, 
  FileTextOutlined,
  CheckCircleOutlined,
  LoadingOutlined 
} from '@ant-design/icons';
import { useNavigate } from 'react-router-dom';
import { useAppStore, useLoading, useError } from '@/stores/appStore';
import type { UploadProps } from 'antd';
import styled from 'styled-components';

const { Dragger } = Upload;
const { Title, Paragraph, Text } = Typography;
const { Step } = Steps;

const UploadContainer = styled.div`
  padding: 24px;
  max-width: 800px;
  margin: 0 auto;
`;

const UploadCard = styled(Card)`
  margin-bottom: 24px;
  
  .ant-upload-drag {
    border: 2px dashed #d9d9d9;
    border-radius: 8px;
    background: #fafafa;
    transition: all 0.3s ease;
    
    &:hover {
      border-color: #1890ff;
      background: #f0f9ff;
    }
  }
  
  .ant-upload-drag-icon {
    font-size: 48px;
    color: #1890ff;
  }
`;

const HelpCard = styled(Card)`
  margin-top: 24px;
`;

const UploadPage: React.FC = () => {
  const navigate = useNavigate();
  const { analyzeFile } = useAppStore();
  const loading = useLoading();
  const error = useError();
  const [uploadProgress, setUploadProgress] = useState(0);

  const handleUpload = useCallback(async (file: File) => {
    try {
      // 
      if (file.size > 50 * 1024 * 1024) {
        message.error(' 50MB');
        return false;
      }

      // 
      if (!file.name.toLowerCase().endsWith('.har')) {
        message.error(' .har ');
        return false;
      }

      // 
      const progressInterval = setInterval(() => {
        setUploadProgress(prev => {
          if (prev >= 90) {
            clearInterval(progressInterval);
            return 90;
          }
          return prev + 10;
        });
      }, 100);

      // 
      await analyzeFile(file);
      
      // 
      setUploadProgress(100);
      clearInterval(progressInterval);
      
      message.success('');
      
      // 
      setTimeout(() => {
        navigate('/analysis');
      }, 1000);

    } catch (err) {
      setUploadProgress(0);
      const errorMessage = err instanceof Error ? err.message : '';
      message.error(errorMessage);
    }

    return false; // 
  }, [analyzeFile, navigate]);

  const uploadProps: UploadProps = {
    name: 'file',
    multiple: false,
    accept: '.har',
    beforeUpload: handleUpload,
    showUploadList: false,
    disabled: loading
  };

  // 
  if (loading) {
    return (
      <UploadContainer>
        <Card>
          <div style={{ textAlign: 'center', padding: '40px 0' }}>
            <Spin 
              size="large" 
              indicator={<LoadingOutlined spin />}
            />
            <Title level={4} style={{ marginTop: 16 }}>
               HAR ...
            </Title>
            {uploadProgress > 0 && (
              <Progress 
                percent={uploadProgress} 
                status={uploadProgress === 100 ? 'success' : 'active'}
                style={{ maxWidth: 400, margin: '16px auto' }}
              />
            )}
            <Paragraph type="secondary">
              
            </Paragraph>
          </div>
        </Card>
      </UploadContainer>
    );
  }

  return (
    <UploadContainer>
      <UploadCard title={
        <Space>
          <FileTextOutlined />
           HAR 
        </Space>
      }>
        {error && (
          <Alert
            message=""
            description={error}
            type="error"
            showIcon
            closable
            style={{ marginBottom: 16 }}
          />
        )}

        <Dragger {...uploadProps}>
          <p className="ant-upload-drag-icon">
            <InboxOutlined />
          </p>
          <p className="ant-upload-text">
             HAR 
          </p>
          <p className="ant-upload-hint">
             .har  50MB
          </p>
        </Dragger>
      </UploadCard>

      <HelpCard title=" HAR ">
        <Steps direction="vertical" size="small">
          <Step
            status="finish"
            title=""
            description=" F12 ''"
            icon={<CheckCircleOutlined />}
          />
          <Step
            status="finish"
            title=" Network "
            description=" Network"
            icon={<CheckCircleOutlined />}
          />
          <Step
            status="finish"
            title=""
            description=""
            icon={<CheckCircleOutlined />}
          />
          <Step
            status="finish"
            title=" HAR "
            description="'Save all as HAR with content'"
            icon={<CheckCircleOutlined />}
          />
        </Steps>

        <Alert
          message=""
          description={
            <div>
              <Paragraph style={{ marginBottom: 8 }}>
                 HAR 
              </Paragraph>
              <Paragraph style={{ marginBottom: 8 }}>
                  HAR 
              </Paragraph>
              <Paragraph style={{ marginBottom: 0 }}>
                 
              </Paragraph>
            </div>
          }
          type="info"
          showIcon
          style={{ marginTop: 16 }}
        />
      </HelpCard>
    </UploadContainer>
  );
};

export default UploadPage;