import React from 'react';
import { Card, List, Badge, Alert, Tag, Divider, Space, Typography } from 'antd';
import { 
  BulbOutlined,
  RocketOutlined,
  SecurityScanOutlined,
  CloudOutlined,
  ThunderboltOutlined,
  SettingOutlined
} from '@ant-design/icons';
import { useAnalysisResult } from '@/stores/appStore';

const { Title, Paragraph, Text } = Typography;

const RecommendationsTab: React.FC = () => {
  const analysisResult = useAnalysisResult();

  if (!analysisResult) {
    return <div></div>;
  }

  const { recommendations, performance, summary } = analysisResult;

  // 
  const categorizedRecommendations = {
    performance: [] as string[],
    security: [] as string[],
    optimization: [] as string[],
    infrastructure: [] as string[]
  };

  // 
  recommendations.forEach(rec => {
    const lowerRec = rec.toLowerCase();
    if (lowerRec.includes('') || lowerRec.includes('') || lowerRec.includes('')) {
      categorizedRecommendations.performance.push(rec);
    } else if (lowerRec.includes('') || lowerRec.includes('https') || lowerRec.includes('ssl')) {
      categorizedRecommendations.security.push(rec);
    } else if (lowerRec.includes('') || lowerRec.includes('') || lowerRec.includes('cdn')) {
      categorizedRecommendations.optimization.push(rec);
    } else {
      categorizedRecommendations.infrastructure.push(rec);
    }
  });

  // 
  const getPriority = (avgResponseTime: number, errorRate: number) => {
    if (avgResponseTime > 2000 || errorRate > 10) return 'high';
    if (avgResponseTime > 1000 || errorRate > 5) return 'medium';
    return 'low';
  };

  const priority = getPriority(summary.avgResponseTime, summary.errorRate);
  const priorityConfig = {
    high: { text: '', color: '#f5222d' },
    medium: { text: '', color: '#faad14' },
    low: { text: '', color: '#52c41a' }
  };

  const renderRecommendationCategory = (
    title: string,
    icon: React.ReactNode,
    items: string[],
    color: string
  ) => {
    if (items.length === 0) return null;

    return (
      <Card 
        title={
          <Space>
            {icon}
            <span>{title}</span>
            <Badge count={items.length} style={{ backgroundColor: color }} />
          </Space>
        }
        style={{ marginBottom: 16 }}
        size="small"
      >
        <List
          size="small"
          dataSource={items}
          renderItem={(item, index) => (
            <List.Item>
              <div style={{ display: 'flex', alignItems: 'flex-start', gap: 8 }}>
                <Badge 
                  count={index + 1} 
                  style={{ backgroundColor: color, fontSize: 12 }}
                />
                <Text>{item}</Text>
              </div>
            </List.Item>
          )}
        />
      </Card>
    );
  };

  return (
    <div style={{ padding: '24px 0' }}>
      {/*  */}
      <Alert
        message={
          <Space>
            <span>:</span>
            <Tag color={priorityConfig[priority].color}>
              {priorityConfig[priority].text}
            </Tag>
          </Space>
        }
        description={
          <div>
            <p>
               <Text strong>{Math.round(summary.avgResponseTime)}ms</Text>
               <Text strong>{summary.errorRate.toFixed(2)}%</Text>
            </p>
            <div style={{ display: 'flex', gap: 16, flexWrap: 'wrap', marginTop: 8 }}>
              <span>: <Badge count={categorizedRecommendations.performance.length} /></span>
              <span>: <Badge count={categorizedRecommendations.security.length} /></span>
              <span>: <Badge count={categorizedRecommendations.optimization.length} /></span>
              <span>: <Badge count={categorizedRecommendations.infrastructure.length} /></span>
            </div>
          </div>
        }
        type={priority === 'high' ? 'error' : priority === 'medium' ? 'warning' : 'success'}
        showIcon
        style={{ marginBottom: 24 }}
      />

      {/*  */}
      {recommendations.length === 0 && (
        <Card>
          <div style={{ textAlign: 'center', padding: '40px 20px' }}>
            <BulbOutlined style={{ fontSize: 48, color: '#52c41a', marginBottom: 16 }} />
            <Title level={4}></Title>
            <Paragraph>
              
              
            </Paragraph>
          </div>
        </Card>
      )}

      {/*  */}
      {renderRecommendationCategory(
        '',
        <RocketOutlined />,
        categorizedRecommendations.performance,
        '#1890ff'
      )}

      {/*  */}
      {renderRecommendationCategory(
        '',
        <SecurityScanOutlined />,
        categorizedRecommendations.security,
        '#f5222d'
      )}

      {/*  */}
      {renderRecommendationCategory(
        '',
        <ThunderboltOutlined />,
        categorizedRecommendations.optimization,
        '#52c41a'
      )}

      {/*  */}
      {renderRecommendationCategory(
        '',
        <CloudOutlined />,
        categorizedRecommendations.infrastructure,
        '#722ed1'
      )}

      {/*  */}
      {recommendations.length > 0 && (
        <Card title={
          <Space>
            <SettingOutlined />
            <span></span>
          </Space>
        }>
          <div>
            <Title level={5}>:</Title>
            <List size="small">
              <List.Item>
                <BulbOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
              <List.Item>
                <BulbOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
              <List.Item>
                <BulbOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
              <List.Item>
                <BulbOutlined style={{ color: '#1890ff', marginRight: 8 }} />
                
              </List.Item>
            </List>

            <Divider />

            <Title level={5}>:</Title>
            <List size="small">
              <List.Item>
                <span style={{ fontWeight: 'bold', color: '#f5222d' }}>:</span> 
                25%
              </List.Item>
              <List.Item>
                <span style={{ fontWeight: 'bold', color: '#faad14' }}>:</span> 
                CDN
              </List.Item>
              <List.Item>
                <span style={{ fontWeight: 'bold', color: '#52c41a' }}>:</span> 
                
              </List.Item>
            </List>

            <Divider />

            <Alert
              message=""
              description=""
              type="info"
              showIcon
            />
          </div>
        </Card>
      )}
    </div>
  );
};

export default RecommendationsTab;